import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import home from '@/views/Home/lessondetails.vue';
import top from '@/views/Home/top.vue';
import signup from '@/views/Home/signup.vue';
import search from '@/views/Search/index.vue';
import message from '@/views/Message/index.vue';
import lesson from '@/views/Lesson/index.vue';
import activity from '@/views/Activity/index.vue';
import mechanism from '@/views/Mechanism/index.vue';
import ask from '@/views/Ask/index.vue';
import askdetails from '@/views/Ask/details.vue';
import comment from '@/views/Ask/Comment.vue';
import hot from '@/components/Ask/Hot.vue';
import new1 from '@/components/Ask/New.vue';
import askfile from '@/views/Ask/Askfile.vue';
import login from '@/views/Login/index.vue';
import setpassword from '@/views/Login/setpassword';
import backpassword from '@/views/Login/backpassword';
import register from '@/views/Login/register.vue';


export default new Router({
  mode:'hash',
  //base:process.env.BABEL_URL,
  base:"lesson",
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: home,
      meta:{keepAlive:true},
      children:[
        {
          path: '/home/top/:cid',
          component: top
        }
      ]
    },
    // 登录
    {
      path: '/login',
      component: login,
    },
    // 设置新密码
    {
      path: '/setpassword',
      component: setpassword,
    },
     // 找回密码
     {
      path: '/backpassword',
      component: backpassword,
    },
      // 注册
      {
        path: '/register',
        component: register,
      },
    
    {
      path: '/home/search',
      component: search
    },
    {
      path: '/signup/:name',
      component: signup
    },
    {
      path: '/message',
      component: message
    },
    {
      path: '/lesson',
      component: lesson,
      meta:{keepAlive:true},
    },
    {
      path: '/mechanism',
      component: mechanism
    },
    {
      path: '/activity',
      component: activity
    },
    {
      path: '/ask',
      component: ask,

    },
    {
      path: '/askfile',
      component: askfile,

    },
    {
      path: '/comment/:id',
      component: comment,

    },
    {
      path: '/askdetails/:id',
      component: askdetails,
      children:[
        // {
        //   path: '/askdetails/new/:id',
        //   name:'new',
        //   component: new1
        // },
        // {
        //   path: '/askdetails/hot/:id',
        //   name:'hot',
        //   component: hot
        // },
        // {
        //   path: '/askdetails/new/:id',
        //   redirect: '/askdetails/new/:id'
        // }
     ]
    }

  ]
})



